import './ColorsBar.css';
import React, { useContext } from 'react';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import Colors from '../../Utils/Colors';
import LinearGradients from '../../Utils/LinearGradients';
import StoreApi from '../../Utils/StoreApi';

export default function ColorsBar({ btn2 }) {
	const { setBgColor, setBgImage } = useContext(StoreApi);

	const btn1 = () => {
		document.querySelector('.sideBar-bar.sideBar-bar-colors').style.width = '0';
	};

	return (
		<div className='sideBar-bar sideBar-bar-colors'>
			<div className='sideBar-head-colors'>
				<ChevronLeftIcon
					id='fa-angle-left'
					onClick={btn1}
				/>
				<h3 className='sideBar-header'>Colors</h3>
				<span
					onClick={btn2}
					className='sideBar-close-btn-colors'>
					×
				</span>
			</div>
			<hr className='sideBar-hr' />
			<div className='sideBar-photos-colors-container overflow'>
				{LinearGradients.map((LinearGradient, index) => {
					const btnLinearGradient = () => {
						setBgImage(LinearGradient);
						setBgColor('');

						localStorage.setItem('storedBgColors', (document.querySelector('.Index').style.backgroundImage = LinearGradient));
					};

					return (
						<div
							className='sideBar-colors-picker'
							onClick={btnLinearGradient}
							key={index}
							style={{ backgroundImage: LinearGradient }}
						/>
					);
				})}
				<hr className='sideBar-hr' />
				{Colors.map((color, index) => {
					const btnColor = () => {
						setBgColor(color);
						setBgImage('');

						localStorage.setItem('storedBgColors', (document.querySelector('.Index').style.backgroundColor = color));
					};

					return (
						<div
							className='sideBar-colors-picker'
							onClick={btnColor}
							key={index}
							style={{ backgroundColor: color }}
						/>
					);
				})}
			</div>
		</div>
	);
}
