import './Wrapper.css';
import React, { useState, useEffect } from 'react';
import { v4 as uuidv4 } from 'uuid';
import Lists from './Lists/Lists';
import Store from '../Utils/Store';
import StoreApi from '../Utils/StoreApi';
import NewLists from './Lists/NewLists';
import ScrollRight from './ScrollBars/ScrollRight';
import ScrollLeft from './ScrollBars/ScrollLeft';
import ScrollToLeft from './ScrollBars/ScrollToLeft';
import ScrollToRight from './ScrollBars/ScrollToRight';
import { DragDropContext, Droppable } from 'react-beautiful-dnd';

export default function Wrapper() {
	const [data, setData] = useState(Store);

	/* Update List Title */

	const updateListTitle = (title, listId) => {
		const list = data.lists[listId];
		list.title = title;

		const newState = {
			...data,
			lists: {
				...data.lists,
				[listId]: list,
			},
		};
		setData(newState);
	};

	/* Add More Lists */

	const addMoreList = (title) => {
		if (title === '') {
			return;
		} else {
			const newListId = uuidv4();

			const newList = {
				id: newListId,
				title,
				cardIds: [],
			};
			const newState = {
				...data,
				lists: {
					...data.lists,
					[newListId]: newList,
				},
				listIds: [...data.listIds, newListId],
			};
			setData(newState);
		}
	};

	/* Delete Lists */

	const deleteList = (listId) => {
		const newLists = data.listIds;
		const newListIds = newLists.filter((list) => list !== listId);

		const newList = Object.keys(data.lists).reduce((acc, key) => {
			// Copy all except listId

			if (key !== listId) {
				acc[key] = data.lists[key];
			}
			return acc;
		}, {});

		const newState = {
			...data,
			lists: {
				...newList,
			},
			listIds: [...newListIds],
		};

		setData(newState);
	};

	/* Update Card Text */

	const updateCardText = (text, cardId) => {
		const card = data.cards[cardId];
		card.text = text;

		const newState = {
			...data,
			cards: {
				...data.cards,
				[cardId]: card,
			},
		};
		setData(newState);
	};

	/* Add More Cards */

	const addMoreCard = (text, listId, cardId) => {
		if (text === '') {
			return;
		} else {
			const newCardId = uuidv4();

			const newCard = {
				id: newCardId,
				text,
			};

			const list = data.lists[listId];
			/*list.cardIds = [...list.cardIds, newCardId];
			data.cards = {
				...data.cards,
				[newCardId]: newCard,
			};
			const newState = {
				...data,
			};*/

			const newState = {
				...data,
				cards: {
					...data.cards,
					[newCardId]: newCard,
				},
				lists: {
					...data.lists,
					[listId]: {
						...list,
						cardIds: [...list.cardIds, newCardId],
					},
				},
			};
			setData(newState);
		}
	};

	/* Delete Cards */

	const deleteCard = (cardId, listId) => {
		const list = data.lists[listId];
		const newCards = list.cardIds;
		const newCardIds = newCards.filter((card) => card !== cardId);

		const newCard = Object.keys(data.cards).reduce((acc, key) => {
			// Copy all except cardId

			if (key !== cardId) {
				acc[key] = data.cards[key];
			}
			return acc;
		}, {});

		const newState = {
			...data,
			cards: {
				...newCard,
			},
			lists: {
				...data.lists,
				[listId]: {
					...list,
					cardIds: [...newCardIds],
				},
			},
		};

		setData(newState);
	};

	/* Drag Lists & Cards */

	const onDragEnd = (result) => {
		const { destination, source, draggableId, type } = result;

		if (!destination) {
			return;
		}

		/* Drag Cards */

		if (type === 'card') {
			const sourceList = data.lists[source.droppableId];
			const destinationList = data.lists[destination.droppableId];

			sourceList.cardIds.splice(source.index, 1);
			destinationList.cardIds.splice(destination.index, 0, draggableId);

			const newState = {
				...data,
				lists: {
					...data.lists,
					[sourceList.id]: sourceList,
					[destinationList.id]: destinationList,
				},
			};
			setData(newState);
		}

		/* Drag Lists */

		if (type === 'list') {
			const newListIds = data.listIds;
			newListIds.splice(source.index, 1);
			newListIds.splice(destination.index, 0, draggableId);
			const newState = {
				...data,
				listIds: newListIds,
			};
			setData(newState);
		}
	};

	/* Move The Scroll By Clicking The Entire Page */

	useEffect(() => {
		const slider = document.querySelector('.Wrapper');
		let mouseDown = false;
		let startX, scrollLeft;

		const startDragging = (e) => {
			/* Prevent Children Effect Except All .Lists */

			if (e.currentTarget /*(.Wrapper)*/ === e.target || e.target.classList.contains('Lists')) mouseDown = true;
			startX = e.pageX - slider.offsetLeft;
			scrollLeft = slider.scrollLeft;
		};
		const stopDragging = () => {
			mouseDown = false;
		};

		const moveDragging = (e) => {
			if (!mouseDown) {
				return;
			}
			const x = e.pageX - slider.offsetLeft;
			const scroll = (x - startX) * 1; //scrollSpeed
			slider.scrollLeft = scrollLeft - scroll;
		};

		/* Add The Event Listeners */

		slider.addEventListener('mousemove', moveDragging);
		slider.addEventListener('mousedown', startDragging);
		slider.addEventListener('mouseup', stopDragging);
		slider.addEventListener('mouseleave', stopDragging);
		return () => {
			slider.removeEventListener('mousemove', moveDragging);
			slider.removeEventListener('mousedown', startDragging);
			slider.removeEventListener('mouseup', stopDragging);
			slider.removeEventListener('mouseleave', stopDragging);
		};
	}, []);

	return (
		<StoreApi.Provider
			value={{
				updateListTitle,
				addMoreList,
				deleteList,
				updateCardText,
				addMoreCard,
				deleteCard,
			}}>
			<DragDropContext onDragEnd={onDragEnd}>
				<Droppable
					droppableId='list'
					direction='horizontal'
					type='list'>
					{(provided) => (
						<div
							className='Wrapper'
							ref={provided.innerRef}
							{...provided.droppableProps}>
							{data.listIds.map((listId, index) => {
								const list = data.lists[listId];
								const cards = list.cardIds.map((cardId) => data.cards[cardId]);
								return (
									<Lists
										list={list}
										listId={list.id}
										key={list.id}
										cards={cards}
										index={index}
									/>
								);
							})}
							{provided.placeholder}
							<NewLists />
						</div>
					)}
				</Droppable>
			</DragDropContext>
			<ScrollRight />
			<ScrollToLeft />
			<ScrollToRight />
			<ScrollLeft />
		</StoreApi.Provider>
	);
}
