import './Lists.css';
import React, { useState, useLayoutEffect, useRef, useContext } from 'react';
import $ from 'jquery';
import ClearIcon from '@mui/icons-material/Clear';
import Cards from './Cards';
import Titles from './Titles';
import StoreApi from '../../Utils/StoreApi';
import { Draggable, Droppable } from 'react-beautiful-dnd';
import InputCards from './InputCards';

export default function List({ list, listId, cards, index }) {
	const [open, setOpen] = useState(false);
	const [text, setText] = useState('');
	const { addMoreCard, deleteList } = useContext(StoreApi);

	const inputRef = useRef(null);

	const mouseEnter = (e) => {
		e.currentTarget./*childNodes[0]*/ /*firstChild*/ firstElementChild.style.display = 'block';
	};

	const mouseLeave = (e) => {
		e.currentTarget./*childNodes[0]*/ /*firstChild*/ firstElementChild.style.display = 'none';
	};

	/* Keep Textarea Height Auto While Typing */

	const [height, setHeight] = useState(0);
	const div = useRef(null);

	useLayoutEffect(() => {
		if (open === true) {
			setHeight(div.current.scrollHeight);
		}
	}, [open, text]);

	/* On Clicking Buttons Events */

	const focusInput = (e) => {
		const slider = document.querySelector('.Wrapper');

		/*Scroll inputCard Into Left View*/

		const inputLeft = e.target /*e.currentTarget*/
			.getBoundingClientRect().left;

		if (slider.scrollLeft <= 24) {
			/*slider.scrollBy*/ slider.scrollTo({
				left: -slider.scrollLeft /*0 With slider.scrollTo (Only)*/,
				behavior: 'smooth',
			});
		} else if (inputLeft < 20) {
			slider.scrollTo({
				left: slider.scrollLeft + inputLeft - 20,
				behavior: 'smooth',
			});
		}

		/*Scroll inputCard Into Right View*/

		if (slider.clientWidth - inputLeft < 268) {
			slider.scrollTo({
				left: slider.scrollLeft + inputLeft - slider.clientWidth + 268,
				behavior: 'smooth',
			});
		}
	};

	const btn1 = () => {
		if (text.trim().length === 0) {
			inputRef.current.focus();
			setText('');
			setOpen(open);
		} else {
			addMoreCard(text.trim(), listId);
			setText('');
			setOpen(!open);
		}
	};

	const btn2 = () => {
		setOpen(!open);
		setText('');
	};

	/* On Key Down Events */

	const keyDown = (e) => {
		if (e.key === 'Enter') {
			e.preventDefault();
			$(document).trigger('click');
			btn1();
		} else if (e.key === 'Escape') {
			$(document).trigger('click');
			setText(text.trim());
			if (text.trim().length === 0) {
				setText('');
			}
		}
	};

	/* Clicking Outside The Div Closing It */

	$(document).on('click', (e) => {
		if (open === true) {
			if ($(e.target).closest('#sideBarBtn-blur').length === 0 && $(e.target).closest('#sideBar-blur').length === 0) {
				if ($(e.target).closest('#bottomBar-blur').length === 0 && $(e.target).closest('#bottomBar-blur2').length === 0) {
					if ($(e.target).closest('#List-blur').length === 0) {
						if ($(e.target).closest('#newList-blur').length === 0 && $(e.target).closest('#newList-blur1').length === 0) {
							setOpen(!open);
							setText('');
						}
					}
				}
			}
		}
	});

	return (
		<Draggable
			draggableId={list.id}
			index={index}>
			{(provided) => (
				<div
					ref={provided.innerRef}
					{...provided.draggableProps}
					className='Lists'>
					<div
						onMouseEnter={mouseEnter}
						onMouseLeave={mouseLeave}
						className='List-wrapper'
						id='List-blur'>
						<div
							onClick={() => deleteList(listId)}
							className='del-list-btn-wrapper'>
							<span className='del-list-btn'>×</span>
						</div>
						<div
							className='Titles'
							{...provided.dragHandleProps}>
							<Titles
								title={list.title}
								listId={list.id}
							/>
						</div>
						<Droppable
							droppableId={list.id}
							index={index}
							type='card'>
							{(provided) => (
								<>
									<div
										className='List overflow'
										ref={provided.innerRef}
										{...provided.droppableProps}>
										{cards.map((card, index) => (
											<Cards
												list={list}
												key={card.id}
												card={card}
												text={card.text}
												listId={listId}
												cardId={card.id}
												index={index}
											/>
										))}
										{provided.placeholder}
										<div>
											{open && (
												<div>
													<div
														className='inputCard-hide overflow'
														ref={div}>
														{text}
													</div>
													<textarea
														style={{ height }}
														autoFocus
														ref={inputRef}
														onKeyDown={keyDown}
														onFocus={focusInput}
														onChange={(e) => setText(e.target.value)}
														placeholder='Enter a title for this card...'
														className='inputCard-input overflow'
														value={text}
														name='text'
														type='text'
														autoComplete='off'
													/>
													<div>
														<button
															className='inputCard-btn'
															onClick={btn1}>
															Add card
														</button>
														<ClearIcon
															className='inputCard-icon'
															onClick={btn2}
														/>
													</div>
												</div>
											)}
										</div>
									</div>
									{setOpen && (
										<InputCards
											open={open}
											setOpen={setOpen}
										/>
									)}
								</>
							)}
						</Droppable>
					</div>
				</div>
			)}
		</Draggable>
	);
}
