import './ScrollToLeft.css';
import React, { useEffect } from 'react';
import KeyboardDoubleArrowLeftIcon from '@mui/icons-material/KeyboardDoubleArrowLeft';

export default function ScrollToLeft() {
	const toggleToLeft = () => {
		const slider = document.querySelector('.Wrapper');
		const scrollLeft = document.querySelector('#scroll-btn-left');

		const scrolledLeft = slider.scrollLeft;
		if (scrolledLeft >= slider.clientWidth / 10) {
			scrollLeft.style.display = 'block';
		} else if (scrolledLeft < slider.clientWidth / 10) {
			scrollLeft.style.display = 'none';
		}
	};

	const scrollToLeft = () => {
		const slider = document.querySelector('.Wrapper');

		/*slider.scrollBy*/ slider.scrollTo({
			left: -slider.scrollLeft /*0 With slider.scrollTo (Only)*/,
			behavior: 'smooth',
		});
	};

	/* Add The Event Listeners */

	useEffect(() => {
		const slider = document.querySelector('.Wrapper');
		slider.addEventListener('scroll', toggleToLeft);
		return () => {
			slider.removeEventListener('scroll', toggleToLeft);
		};
	}, []);

	return (
		<div
			id='scroll-btn-left'
			onClick={scrollToLeft}>
			<KeyboardDoubleArrowLeftIcon />
		</div>
	);
}
