import './Cards.css';
import React, { useContext, useState, useLayoutEffect, useRef } from 'react';
import $ from 'jquery';
import CloseIcon from '@mui/icons-material/Close';
import StoreApi from '../../Utils/StoreApi';
import { Draggable } from 'react-beautiful-dnd';

export default function Cards({ list, card, text, listId, cardId, index }) {
	const [open, setOpen] = useState(false);
	const [newText, setNewText] = useState(text);
	const { updateCardText, deleteCard } = useContext(StoreApi);

	const inputRef = useRef(null);

	/* Keep Textarea Height & Top Auto While Typing */

	const [height, setHeight] = useState(0);
	const divRef = useRef(null);

	const [top, setTop] = useState(0);

	useLayoutEffect(
		(e) => {
			if (open === true) {
				setHeight(divRef.current.scrollHeight);
				const card = document.getElementsByClassName('card-textarea');
				/*Select .cards-wrapper */
				let nextSibling = document.querySelector('.card-dark').nextElementSibling;
				/*Get .cards-wrapper Top*/
				const cardWrapperOffsetTop = nextSibling.getBoundingClientRect().top;
				/*Get .card-textarea Height*/
				const cardOffsetHeight = card[0].getBoundingClientRect().height;
				/*Get Document Height*/
				const docHeight = document.documentElement.clientHeight;

				const value = docHeight - cardOffsetHeight;

				value > cardWrapperOffsetTop ? setTop(cardWrapperOffsetTop) : setTop(value);
				/*if (value > cardWrapperOffsetTop) {
					setTop(cardWrapperOffsetTop);
				} else {
					setTop(value);
				}*/
			}
		},
		[newText, open, height],
	);

	/* On Clicking Buttons Events */

	const btn1 = (e) => {
		/* Prevent Children Effect Except All .card-textarea Children */

		if (e.currentTarget /*(.card-dark)*/ === e.target || e.target.classList.contains('card-textarea')) setOpen(!open);
		$(document).trigger('click');
		setNewText(text);
	};

	const btn2 = () => {
		setOpen(!open);
		setNewText(text);
	};

	const btn3 = (e) => {
		e.stopPropagation();
		$(document).trigger('click');
		setNewText(newText.trim());

		if (newText.trim().length === 0) {
			inputRef.current.focus();
			setNewText(newText);
		} else {
			setOpen(!open);
			updateCardText(newText.trim(), cardId);
		}
	};

	/* On Clicking Cards Events */

	const [left, setLeft] = useState(0);

	const btn4 = (e) => {
		/* Prevent Children Effect */

		if (e.currentTarget /*(.card-dark)*/ === e.target) {
			setOpen(!open);

			//e.target /*e.currentTarget*/ .scrollIntoView();

			const slider = document.querySelector('.Wrapper');

			/*Set Card Position Top*/

			const cardTop = e.target /*e.currentTarget*/
				.getBoundingClientRect().top;

			setTop(cardTop);

			/*Scroll Card Into Left View*/

			const cardLeft = e.target /*e.currentTarget*/
				.getBoundingClientRect().left;

			setLeft(cardLeft - 2.25);

			if (cardLeft < 20) {
				slider.scrollTo({
					left: slider.scrollLeft + cardLeft - 20,
					behavior: 'smooth',
				});
				setLeft(18.25);
			}

			/*Scroll Card Into Right View*/

			if (slider.clientWidth - cardLeft < 268) {
				slider.scrollTo({
					left: slider.scrollLeft + cardLeft - slider.clientWidth + 268,
					behavior: 'smooth',
				});
				setLeft(slider.clientWidth - 270.25);
			}
		}
	};

	const contextMenu = (e) => {
		/* Prevent Children Effect */

		if (e.currentTarget /*(.card-dark)*/ === e.target) {
			setOpen(!open);
			e.preventDefault();

			//e.target /*e.currentTarget*/ .scrollIntoView();

			const slider = document.querySelector('.Wrapper');

			/*Set Card Position Top*/

			const cardTop = e.target /*e.currentTarget*/
				.getBoundingClientRect().top;

			setTop(cardTop);

			/*Scroll Card Into Left View*/

			const cardLeft = e.target /*e.currentTarget*/
				.getBoundingClientRect().left;

			setLeft(cardLeft - 2.25);

			if (cardLeft < 20) {
				slider.scrollTo({
					left: slider.scrollLeft + cardLeft - 20,
					behavior: 'smooth',
				});
				setLeft(18.25);
			}

			/*Scroll Card Into Right View*/

			if (slider.clientWidth - cardLeft < 268) {
				slider.scrollTo({
					left: slider.scrollLeft + cardLeft - slider.clientWidth + 268,
					behavior: 'smooth',
				});
				setLeft(slider.clientWidth - 270.25);
			}
		}
	};

	/* On Key Down Events */

	const keyDown = (e) => {
		if (e.key === 'Enter') {
			e.preventDefault();
			$(document).trigger('click');
			setNewText(newText.trim());

			if (newText.trim().length === 0) {
				inputRef.current.focus();
				setNewText(newText);
			} else {
				setOpen(!open);
				updateCardText(newText.trim(), cardId);
			}
		} else if (e.key === 'Escape') {
			$(document).trigger('click');
			setOpen(!open);
			setNewText(text);
		}
	};

	const mouseEnter = (e) => {
		e.currentTarget./*childNodes[0]*/ /*firstChild*/ firstElementChild.style.display = 'block';
	};

	const mouseLeave = (e) => {
		e.currentTarget./*childNodes[0]*/ /*firstChild*/ firstElementChild.style.display = 'none';
	};

	return (
		<Draggable
			draggableId={card.id}
			index={index}>
			{(provided) => (
				<div>
					{open && (
						<div
							className='card-dark'
							onClick={btn1}>
							<span
								className='card-close'
								onClick={btn2}>
								<CloseIcon sx={{ '&:hover': { color: '#fff9' } }} />
							</span>
							<div
								style={{ top, left }}
								className='card-textarea'>
								<div
									className='cards-wrapper-hide'
									ref={divRef}>
									{newText}
								</div>
								<textarea
									onClick={(e) => e.stopPropagation()}
									className='card-input'
									style={{ height }}
									value={newText}
									ref={inputRef}
									onKeyDown={keyDown}
									onChange={(e) => setNewText(e.target.value)}
									autoFocus
									onFocus={(e) => e.target.select()}
									name='text'
									type='text'
									autoComplete='off'
								/>
								<button
									className='card-btn'
									onClick={btn3}>
									Save
								</button>
							</div>
						</div>
					)}{' '}
					{setOpen && (
						<div
							key={index}
							onClick={btn4}
							onMouseEnter={mouseEnter}
							onMouseLeave={mouseLeave}
							onContextMenu={contextMenu}
							className='cards-wrapper'
							ref={provided.innerRef}
							{...provided.dragHandleProps}
							{...provided.draggableProps}>
							<div
								onClick={() => deleteCard(cardId, listId)}
								className='del-card-btn-wrapper'>
								<span className='del-card-btn'>×</span>
							</div>
							{text}
						</div>
					)}
				</div>
			)}
		</Draggable>
	);
}
