import './Bars.css';
import React from 'react';
import BottomBar from './BottomBar';
import SideBars from '../SideBar/SideBars';

export default function Bars({ barTitle, barId }) {
	return (
		<>
			<div className='bars-toolbar'>
				<div className='bars-btn'>
					<div className='trello' />
				</div>
				<SideBars />
			</div>
			<BottomBar
				barTitle={barTitle}
				barId={barId}
			/>
		</>
	);
}
