import './ScrollToRight.css';
import React, { useEffect } from 'react';
import KeyboardDoubleArrowRightIcon from '@mui/icons-material/KeyboardDoubleArrowRight';

export default function ScrollToRight() {
	const toggleToRight = () => {
		const slider = document.querySelector('.Wrapper');
		const scrollRight = document.querySelector('#scroll-btn-right');

		const scrolledRight = slider.scrollWidth - slider.scrollLeft - slider.clientWidth;
		if (scrolledRight >= slider.clientWidth / 10) {
			scrollRight.style.display = 'block';
		} else if (scrolledRight < slider.clientWidth / 10) {
			scrollRight.style.display = 'none';
		}
	};

	const scrollToRight = () => {
		const slider = document.querySelector('.Wrapper');

		/*slider.scrollBy*/ slider.scrollTo({
			left: slider.scrollWidth /*1e9*/,
			behavior: 'smooth',
		});
	};

	/* Add The Event Listeners */

	useEffect(() => {
		const slider = document.querySelector('.Wrapper');
		slider.addEventListener('scroll', toggleToRight);
		return () => {
			slider.removeEventListener('scroll', toggleToRight);
		};
	}, []);

	return (
		<div
			id='scroll-btn-right'
			onClick={scrollToRight}>
			<KeyboardDoubleArrowRightIcon />
		</div>
	);
}
